<template>
  <el-select
    v-bind="[$props, delProps]"
    :size="irdSize"
    :style="styles"
    :collapse-tags="collapse || collapseTags"
    :disabled="disAll || disabled"
    @focus="focus"
    @change="change"
    @input="input($event)"
  >
    <el-option
      v-for="(item, index) of data"
      :disabled="
        typeof item.disabled === 'boolean'
          ? item.disabled
          : item[type['id']] === disId
      "
      :label="renderSelfValue(item[type['id']],item[type['name']])"
      :value="item[type['id']]"
      :key="item[type['id']] + '--' + index"
    >
      {{ renderSelfLabel(item[type['id']], item[type['name']], keyValue) }}
    </el-option>
  </el-select>
</template>

<script>
  import { Select, Option } from "element-ui";

  export default {
    name: "IrdSelect",
    components: {
      [Select.name]: Select,
      [Option.name]: Option,
    },
    props: {
      ...Select.props,
      value: [String, Number, Array],
      clearable: {
        type: Boolean,
        default: true,
      },
      multiple: {
        type: Boolean,
        default: false,
      },
      collapse: {
        type: Boolean,
        default: false,
      },
      collapseTags: {
        type: Boolean,
        default: false,
      },
      filterable: {
        type: Boolean,
        default: true,
      },
      reserveKeyword: {
        type: Boolean,
        default: true,
      },
      keyValue: {
        type: Boolean,
        default: true,
      },
      disId: [String, Number], // IDID 
      disAll: [Boolean], // 
      disabled: [Boolean], // 
      type: {
        // 
        type: Object,
        default() {
          return {
            name: "name",
            id: "id",
          };
        },
      },
      data: Array,
      placeholder: {
        type: String,
        default: "",
      },
      styles: {
        type: Object,
        default() {
          return {
            width: "161px",
          };
        },
      },
      renderValue: Function,
      renderLabel: Function,
    },
    computed: {
      delProps() {
        let ary = {};
        let delAry = ["data", "type", "styles"];
        delAry.forEach((key) => {
          ary[key] = null;
        });
        return ary;
      },
      irdSize() {
        return this.size || this.$IRDD?.size;
      },
    },
    methods: {
      renderSelfValue(id, val) {
        if (this.renderValue) return this.renderValue(id, val);
        return val;
      },
      renderSelfLabel(id, val, all) {
        if (this.renderLabel) return this.renderLabel(id, val);
        if (all) {
          return id + "" + val;
        }
        return val;
      },
      input(val) {
        this.$emit("input", val);
      },
      focus(ev) {
        this.$emit("onFocus", ev); // 
        this.$emit("focus", ev);
      },
      change(val) {
        this.$emit("onChange", val); // 
        this.$emit("change", val);
      },
    },
  };
</script>
