<template>
  <div>
    <div @click="copy($event, 12112122)">121212</div>
    <slot></slot>
  </div>
</template>

<script>
  import Clipboard from "clipboard";

  export default {
    name: "",
    mounted() {
      console.log(this.$slots.default);
    },
    methods: {
      copy(ev, text) {
        const clipboard = new Clipboard(ev.target, { text: () => text });
        clipboard.on("success", (e) => {
          console.log("");
          clipboard.destroy();
        });
        clipboard.on("error", (e) => {
          // 
          console.log("");
          // 
          clipboard.destroy();
        });
      },
    },
  };
</script>

