export const PAGEPARAMS = {
  pageSize: 1000, //
  pageNum: 1, //
};
export const STARTTIME = "00:00:00";
export const ENDTIME = "23:59:59";
export const DATE_FORMAT = "YYYY-MM-DD";
export const DATE_TIME_FORMAT = "YYYY-MM-DD HH:mm:ss";

/**
 * 
 * @param  {function} promise 
 */
export const awaitWrap = (promise) => {
  return promise
    .then(data => [null, data])
    .catch(err => [err, null]);
};
/**
 * id
 * @param  {number} length   
 */
export const genID = (length = 3) => {
  return Number(Math.random().toString().substr(3, length) + Date.now()).toString(16);
};

/**
 * 
 * @param  {string} email url
 */
export function isEmail(email) {
  const reg = new RegExp("^\\w+([-+.]\\w+)*@\\w+([-.]\\w+)*\\.\\w+([-.]\\w+)*$");
  return reg.test(email);
}

/**
 * InternetURL
 * @param  {string} url InternetURLurl
 */
export function isInternetURL(url) {
  const reg = new RegExp("[a-zA-z]+://[^\\s]*");
  return reg.test(url);
}

/**
 *   
 * @param  {string} str InternetURLurl
 */
export const getBLen = (str) => {
  if (str == null) return 0;
  return str.replace(/[^\x00-\xff]/g, "aa").length;
};

/**
 * 
 * @param  {number | string} num 
 * @param  {number} val  2
 */
export const formatterNumber = (num, val = 2) => {
  if (Rules.thousandNumReg.test(num)) return num; // 
  let nwe_num = parseFloat(num, 10); // 
  if (isNaN(nwe_num)) return num; // NaN
  const [integer, decimal = ""] = nwe_num.toLocaleString().split(".");

  if (val === 0) return integer;
  return integer + "." + decimal.padEnd(val, "0");
};

/*  */
export const Rules = {
  thousandNumReg: /^(-)?\d{1,3}(,\d{3})+(.\d+)?$/,// 
  phoneReg: /^1\d{10}$/,// 
  emojiReg: /(\ud83c[\udf00-\udfff])|(\ud83d[\udc00-\ude4f])|(\ud83d[\ude80-\udeff])/,// emoji
};

/**
 * 
 * @param val 
 * @param position 
 */
export const returnFloat = (val = 0, position) => {
  if ((val == undefined || val == "null") && position) {
    return position;
  }
  val = val || 0;
  let value = Math.round(parseFloat(val) * 100) / 100;
  let xsd = value.toString().split(".");
  if (xsd.length === 1) {
    return value.toString() + ".00";
  }
  if (xsd.length > 1) {
    if (xsd[1].length < 2) {
      value = value.toString() + "0";
    }
    return value;
  }
};

/**
 *  v1.0rc10 
 * with-router item enums   
 * @param fields 
 * @param key key
 * @param data enumsdataenums
 */
export const changeFields = (fields, key, data) => {
  if (!fields.length) return;

  if (!data) {
    return fields.find((item) => item.key === key);
  }
  for (let i = 0; i < fields.length; i++) {
    if (fields[i].key === key) {
      fields[i].enums = data;
      break;
    }
  }
};

/**
 * with-router item  enums   
 * @param fields 
 * @param key key
 * @param opt 1 enums 2 key  fields value  3 fields
 */
export const handleFields = (fields, key, opt) => {
  if (!opt) return fields.find((item) => item.key === key); // 
  let data;
  if (Object.prototype.toString.call(opt).slice(8, -1) === "Object" && opt.key) {
    data = {
      key: opt.key || "enums",
      value: opt.value,
    };
  } else {
    data = {
      key: "enums",
      value: opt?.value || opt,
    };
  }

  if (!fields.length) return;
  for (let i = 0; i < fields.length; i++) {
    if (fields[i].key === key) {
      fields[i][data.key] = data.value;
      break;
    }
  }
};