export const generateData = () => {
  const data = [];
  const cities = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const pinyin = [
    "sanguo",
    "shuihu",
    "xiyouji",
    "honhlumeng",
    "zhouyi",
    "shiing",
    "guiguzi",
    "sunzibingfa",
    "shiji",
  ];
  cities.forEach((city, index) => {
    data.push({
      label: city,
      key: index,
      pinyin: pinyin[index],
      disabled: index % 4 === 0,
    });
  });
  return data;
};
