import table from "@/mixins/table.mixins";
import { TableColumn, Button, Pagination, Popconfirm } from "element-ui";

export default {
  name: "EnhanceTable",
  components: {
    [TableColumn.name]: TableColumn,
    [Popconfirm.name]: Popconfirm,
    [Button.name]: Button,
    [Pagination.name]: Pagination,
  },
  mixins: [table],
  props: {
    otherTableParams: {
      // table
      type: Object,
      default: function() {
        return {};
      },
    },
    otherPaginationParams: {
      // 
      type: Object,
      default: function() {
        return {};
      },
    },
    tableColumn: {
      // tablecolumn
      type: Array,
      default: function() {
        return [];
      },
    },
    multiple: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    irdSize() {
      return this.size || this.$IRDD?.size;
    },
  },
  methods: {
    // 
    tableRef() {
      return this.$refs["tableRef"];
    },
    handleButtons(item, record) {
      //  -> jsx
      if (item.handleButtons) return item.handleButtons(record);

      return (
        !!item.list.length &&
        item.list.map((item) => {
          if (item.confirm) {
            return (
              <el-popconfirm
                icon="el-icon-info"
                iconColor="#f56c6c"
                title={item.confirmTip || ""}
                on={{
                  confirm: () => item.cb && item.cb(record),
                }}
                scopedSlots={{
                  reference: () => {
                    return (
                      <el-button
                        disabled={item.disabled}
                        style={item.styles}
                        icon={item.icon}
                        type="text"
                        size={this.irdSize}
                      >
                        {item.title}
                      </el-button>
                    );
                  },
                }}
              />
            );
          }

          return (
            <el-button
              disabled={item.disabled}
              key={item.title}
              style={item.styles}
              icon={item.icon}
              type="text"
              size={this.irdSize}
              onClick={() => item.cb && item.cb(record)}
            >
              {item.title}
            </el-button>
          );
        })
      );
    },

    // handleCurrentChange(val) {
    //   this.$emit('currentChange', val);
    // },
  },
  mounted() {
  },
  render() {
    const {
      tableOptions,
      paginationOptions,
      paginationOptionsMethod,
      tableOptionsMethod,
      otherTableParams,
      otherPaginationParams,
      tableColumn,
      multiple,
    } = this;

    return (
      <div class={"ird-table"}>
        <el-table
          ref={"tableRef"}
          {...{
            props: {
              size: this.irdSize,
              ...tableOptions,
              ...otherTableParams.props,
            },
            on: {
              ...tableOptionsMethod,
              ...otherTableParams.on,
            },
          }}
        >
          {/* table */}
          {multiple && (
            <el-table-column type="selection" width="55" fixed={"left"} />
          )}
          {/* table */}
          {tableColumn.map((item) => {
            if (item.type === "button") {
              // 
              return (
                <el-table-column
                  {...{
                    props: {
                      label: item.label || "",
                      width: item.width || "88",
                      fixed: item.fixed || "right",
                      ...item,
                    },
                    scopedSlots: {
                      default: ({ row }) => {
                        return this.handleButtons(item, row);
                      },
                    },
                  }}
                />
              );
            }
            return (
              <el-table-column
                {...{
                  props: {
                    key: item.prop,
                    ...item,
                  },
                  // 
                  scopedSlots: {
                    default: ({ row, $index }) => {
                      return item.render
                        ? item.render(row[item.prop], row, $index, item.prop)
                        : row[item.prop];
                    },
                  },
                }}
              />
            );
          })}
        </el-table>
        {/**/}
        <el-pagination
          {...{
            class: paginationOptions.class, // 
            props: {
              ...paginationOptions,
              ...otherPaginationParams.props,
            },
            on: {
              ...paginationOptionsMethod,
            },
          }}
        />
      </div>
    );
  },
};
